@media screen and (max-width: 1330px) {

  .matrix-of-the-year_container {
    .table-wrapper {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 160px;
      gap: 10px;
      margin-top: 50px;
    }

    .matrix-of-the-year_result {
      display: flex;
      flex-direction: column;
      padding: 0px;
      height: auto;
      //border: 1px solid green;


      @media screen and (max-width: 800px) {
        height: 750px;
        margin-top: 30px;
        .matrix-compatibility {
          //height: 300px;
          //width: 380px;
        }
        .purposes {
          width: 100%;
          position: relative;
          left: 0%;
          top: -1%;

          .table-wrapper {
            gap: 10px;
          }

          h3 {
            font-family: 'Playfair Display', serif;
            margin: 0;
            color: black;
          }

          .purposes_top {
            display: flex;
            justify-content: center;
            align-items: center;
            margin: 10px 0;
            gap: 5px;

            .purpose {
              //border: 1px solid blue;
              .circle_purpose {
                width: 30px;
                height: 30px;
                background: white;
                color: black;
              }
              .circle_sum {
                background: white;
                color: black;
              }
            }

          }

          .purpose_general {
            //border: 1px solid blue;
            height: 80px;
            margin-top: -5px;
            margin-bottom: -20px;

            .circle_solo {
              .circle {
                width: 30px;
                height: 30px;
                background: white;
                font-size: 18px;
                color: black;
              }
            }

          }
        }
      }

      //.matrix-compatibility {
      //  min-height: 300px;
      //  overflow: visible;
