.table-child_container {
  display: flex;
  flex-direction: column;
  width: 500px;
  @media screen and (max-width: 1624px) {
    width: 390px;
  }

  .table-child_h3 {
    width: 90%;
    margin: 0 auto 25px;
    font-size: 18px;
    font-family: 'Playfair Display', serif;
    color: #263E80;
    @media screen and (max-width: 1624px) {
      font-size: 16px;
    }

  }

  .table-child {
    position: relative;
    border-radius: 25px;
    height: 410px;
    overflow: hidden;

    &::before {
      content: "";
      background-image: url('../../assets/bg-new-h-nogrid-1920x1095.png');
      background-size: 100%;
      position: absolute;
      width: 500px;
      height: 280px;
      top: 0px;
      right: 0px;
      bottom: 0px;
      left: 0px;
      opacity: 0.5;
    }

    .table-child_row {
      z-index: 2;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 100%;
      margin-bottom: 7px;
      border-top: 1px solid rgba(69, 69, 69, 0.15);


      .table-child_column {
        z-index: 2;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        width: 25%;
        font-family: 'Playfair Display', serif;
        font-size: 17px;
        text-align: center;

      }

    }

    .table-child_row-header {
      position: relative;
      border-top: none;
      margin-bottom: 10px;
      margin-top: 20px;
      padding: 10px 0;

      &::before {
        content: "";
        background-image: url('../../assets/rule-a.png');
        background-size: 100%;
        position: absolute;
        width: 500px;
        height: 40px;
        top: 350px;
        right: 0px;
        bottom: 0px;
        left: 0px;
        opacity: 0.5;
        @media screen and (max-width: 1624px) {
          width: 390px;
        }
      }
    }

  }
}